@import './sass/variables';

.container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    width: 100%;
    border: 1px solid var(--color-border);
    border-radius: var(--radius-16);
    box-shadow: var(--box-shadow-24);
    background-color: var(--color-surface-1);
    margin-bottom: 20px;
}
